
/*Comun*/
.container {
    max-width: 100%;
}

.content-title {
   font-family: 'McLaren', cursive;
   color: #9F1B12;}

h3 {
    font-size: 35px;
    font-weight: bold;
}

/*comun*/

/*Inicia Menu*/
.menu {
    height: 100%;
    width: 200px;
    display: block;
    position: fixed;
    color: #101C24;
}

a {
    overflow: hidden;
}

a:hover {
    color: #F7DEDD;
}

a:active, a:focus, a:hover, a:link {
    text-decoration: none;
    background-color: transparent;
    cursor: pointer; 
}

.menu-link {
    font-family: 'Lato', sans-serif;
    font-weight: bold;
    font-style: italic;
    color: #101C24;
    width: 100%;
    font-size: 18px;  
}

.menu-name {
    font-size: 1em;
}

.menu-2 {
    display:none;
}
/*Termina Menu*/

/*Inicia Estilos del contenido*/
.topics {
    padding: 0;
}

.subtopic {
    margin: 0px 0px;
    max-width: 100%;
}
/*Termina Estilos de contenido*/

/* Inician estilos de seccion aboutme*/
.about {
    color: #C71D17;
    border-radius: 20px 5px 0px 0px;
    -moz-border-radius: 20px 5px 0px 0px;
    -webkit-border-radius: 20px 5px 0px 0px;
}

.image-aboutme{
    font-family: 'Amaranth', sans-serif;
}

h2 {
    font-weight: bolder;
    font-size: 42px;
    text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
                 0px -5px 35px rgba(255,255,255,0.3);    
}


h5.title {
    font-weight: light;
    font-style: italic;
    color: #C79217;
    font-size: 30px;
    text-shadow: 0px 0px 6px #550C09;
}


.content-aboutme {
    font-family: 'Titillium Web', sans-serif;
    text-align: center; 
    font-size: 18px;
    font-weight: light; 
    color: #F7DEDD;
    height: 40%;   
}

.photo {
    display: flex;
    align-items: center;
    justify-content: center;
}
/*Terminan estilos de seccion aboutme*/

/*Inician estilos de sección work*/
.content-work {
    color: #9F1B12;
}

.card-work {
    background: none;
    border: 2px solid #C79217;
    font-family: 'Titillium Web', sans-serif;
    text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
                 0px -5px 35px rgba(255,255,255,0.3);
}

.card-title {
    color: #9F1B12;
    font-size: 30px;
    font-weight: bold; 
}


.card-text {
    color: #F7DEDD;
    font-size: 18px;
}

.card-link {
    color: #101C24;
    font-family: 'Lato', sans-serif;
    font-size: 19px;
}

/*Terminan estilos de sección work*/


/*Inician estilos de sección skills*/
.skills {
    color: #9F1B12;
}

.card-skill {
    background: none;
    border: none;
    padding: 0px 40px;
}

.name-skill {
    color: #C79217;
    font-size: 30px;
}

.list-skill {
    color: #F7DEDD;
    font-family: 'Titillium Web', sans-serif;
    font-size: 18px;
    padding: 0px 80px;
    list-style: none;
}


/*Terminan estilos de sección skills*/

/*Inician estilos de sección contact*/
.contact {
    color: #9F1B12;
    border-radius: 1px 0px 5px 20px;
    -moz-border-radius: 1px 0px 5px 20px;
    -webkit-border-radius: 1px 0px 5px 20px;
}


.content-contact {
    font-family: 'Titillium Web', sans-serif;
    color: #F7DEDD;
    font-size: 18px;
}

.contact-icons {
    display: inline-block;
}

.contact-link {
    font-size: 2em;
    padding: 10px 10px;
    text-decoration: none;
    background-color: transparent;
    cursor: pointer;
    color: #C79217;
}

/*Terminan estilos de sección contact*/


/*Inicia configuración para versión móvil*/

@media only screen and (max-width: 767.98px){
    .menu-1 {
        display: none;
    }

    .menu-2 {
        display: inline-block;
    }

    .menu {
        height: 10%;
        width: 90%;
        position: fixed; 
    }

    .menu a {
        background-color:#7BC8E1;
        opacity: 0.6;
    } 

    .description {
       padding: 10px 5px !important;
   }

   .content-aboutme {
       margin: 0px 5px;
   }

   .photo {
       padding: 20px 0px; 
   }

   .about {
       padding-top: 30px;
   }
   
}


